<template>
  <div id="app">
    <header>
      <h1>Played It</h1>
    </header>
    <main>
      <div class="content">
        <router-view></router-view>
      </div>
    </main>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  data () {
    return {
      posts: [],
      endpoint: 'https://jsonplaceholder.typicode.com/posts/',
    }
  },

  created() {
    this.getAllPosts();
  },

  methods: {
    getAllPosts() {
      axios.get(this.endpoint)
        .then(response => {
          this.posts = response.data;
        })
        .catch(error => {
          console.log('-----error-----');
          console.log(error);
        })
    }
  }
}
</script>

<style lang="scss">
</style>



